
import 'chartjs-adapter-moment';

import Modal from "@/components/common/Modal.vue";

import {defineComponent, onMounted, onUnmounted, ref, watch} from "vue";
import moment, {Moment} from "moment";
import Remote from "@/remote/scheduleRequest";
import {EmployeeGetters} from "@/store/employeeModule";
import Dialog from "@/components/dialog";
import {entries} from "lodash";
import currency from 'currency.js';
import Chart from "chart.js/auto/auto.mjs";
import {getDateMockEmployeeUnit, getDateMockEmployeeValue} from "@/components/Analysis/AnalysisMockData";
import {nameOfDays} from "@/components/common/script";
import CustomSelect from "@/components/common/CustomSelect.vue";
import {AnalysisActions} from "@/store/analysisModule";
import { useStore } from 'vuex';
let MyChart = <any>null;

declare var Affinity: any


export default defineComponent ({
    name: "EmployeeWeekly",
    components: {
        CustomSelect,
        Modal,
        Dialog,
        // MultiSelect,
    },
    props: {
        dayFocus: {
            type: Object
        },
        fromDate: {
            type: Object,
        },
        toDate: {
            type: Object,
        },
        selectedUnit: {
            type: String,
        },
        modalIndex: {
            type: Number
        },
        employeeList: {
            type: Array
        },
    },

    setup(props) {
        const store = useStore();
        const selectedDate = ref<Moment | null>(null);
        const data = ref<any[]>([]);
        const formattedData = ref<any[]>([]);
        const dataHours = ref<any[]>([]);
        const dataUnits = ref<any[]>([]);
        const unitOptions = ref<string[]>(['hours', 'unit']);

        const calculatedData = ref<any[]>([]);
        const total = ref<any>({});
        const totalInNumber = ref<any>({});
        const displayN = ref<number>(6); // number of column to display,

        const displayList = ref<any>({});
        const sourceDisplayList = ref<any>({
            scheduled: {
                heading: 'Scheduled',
                isDisplay: true,
                isCalculated: false,
            },
            actual: {
                heading: 'Actuals',
                isDisplay: false,
                isCalculated: false,
            },
            budgeted: {
                heading: 'Budgeted',
                isDisplay: false,
                isCalculated: false,
            },
            sales: {
                heading: 'Sales',
                isDisplay: false,
                isCalculated: false,
            },
        });

        const weekStart = ref<string>('');
        const weekEnd = ref<string>('');
        const noDataError = ref(false);


        onMounted(() => {
            loadDataBySelection();
        });

        onUnmounted(() => {
            MyChart.destroy();
            MyChart = null;
        });

        const loadDataBySelection = () => {
            let weekStart = (props.fromDate as any).clone().startOf('week').format("YYYY-MM-DD");

            let weekEnd =  (props.toDate as any).clone().endOf('week').format("YYYY-MM-DD");
            Dialog.loading()
            store.dispatch(AnalysisActions.fetchEmployeeWeeklyAnalysisData,  {
                weekStart,
                weekEnd,
                employeeNumbers: props.employeeList!.filter((employeeNumber: any) => employeeNumber !== null && employeeNumber !== undefined),
                selectedUnit: props.selectedUnit,
                pageNumber: 1,
                pageSize: 99
            }).then((res: any) => {
                console.log('res', res.response)
                displayList.value = ['Scheduled', 'Actual', 'Variance'];
                data.value = res.response.map((data: any) => {
                    return [data.employee, data.scheduled, data.actual, data.variance]
                        // currency(data.scheduled).format({symbol: ''}),
                        // currency(data.actual).format({symbol: ''}),
                        // currency(data.variance).format({symbol: ''})]
                })
                formattedData.value = res.response.map((data: any) => {
                    return [data.employee,
                        currency(data.scheduled).format({symbol: ''}),
                        currency(data.actual).format({symbol: ''}),
                        currency(data.variance).format({symbol: ''})]
                })
                setGraphData();
                Dialog.clear();
            })

        }

        const setGraphData = () => {
            let chartLabels = []
            let chartDatasets = [{
                label: 'Scheduled',
                data: <any[]>[],
                backgroundColor: [
                    'rgba(255, 99, 132)',
                ],
            }, {
                label: 'Actual',
                data: <any[]>[],
                backgroundColor: '#93e093'
            }]

            chartLabels = data.value.map((data: any) => {
                return data[0] // employee name
            })

            for (const dayData of data.value) {
                chartDatasets[0].data.push(dayData[1])
                chartDatasets[1].data.push(dayData[2])
            }

            let ctx = (document as any).getElementById('employeeWeeklyChart').getContext('2d');

            let config = {
                type: 'bar',
                data: {
                    labels: chartLabels,
                    datasets: chartDatasets,
                },

                options: {
                    responsive: true,
                }
            }
            if (MyChart) {
                console.log('my chart', MyChart)
                // handle switch tab that only destroy chart
                if (MyChart.ctx === null) {
                    MyChart = new Chart(ctx!, config as any ) as any;
                } else {
                    MyChart.update();
                }

            } else {
                MyChart = new Chart(ctx!, config as any ) as any;
            }
        }

        async function exportCSV() {
            if (noDataError.value) {
                Dialog.alert("No data to export");
                return;
            }

            Dialog.loading();
            // const allData: any = await loadDataBySelection();
            const allData: any = data.value;
            Dialog.clear();
            let csvContent = "data:text/csv;charset=utf-8,";

            csvContent +=  "," + displayList.value.join(",") + "\r\n";

            allData.map((rowArray: any) => {
                csvContent += Object.values(rowArray).join(",") + "\r\n";
            });

            var encodedUri = encodeURI(csvContent);
            var link = document.createElement("a");
            link.setAttribute("href", encodedUri);
            link.setAttribute("download", "Analytic.csv");
            document.body.appendChild(link); // Required for FF

            link.click();
        }


        return {
            selectedDate,
            data,
            formattedData,
            dataHours,
            dataUnits,
            unitOptions,
            calculatedData,
            total,
            totalInNumber,
            displayN,
            displayList,
            sourceDisplayList,
            weekStart,
            weekEnd,
            loadDataBySelection,
            setGraphData,
            exportCSV,
        }
    },
})
