
import "chartjs-adapter-moment";

import Modal from "@/components/common/Modal.vue";

import {defineComponent, getCurrentInstance, onMounted, ref} from "vue";
import CustomSelect from "@/components/common/CustomSelect.vue";
import moment, {Moment} from "moment";
import Dialog from "@/components/dialog";
import {EmployeeActions} from "@/store/employeeModule";
import {useStore} from "vuex";
import DatePicker from "@/components/common/DatePicker.vue";
import DetailCostReport from "@/components/Analysis/DetailCostReport.vue";
import SimpleAnalysis from "@/components/Analysis/SimpleAnalysis.vue";
import {SelectableView} from "@/components/Analysis/CostAnalysisType";
import CostAnalysisWeekly from "@/components/Analysis/CostAnalysisWeekly.vue";
import EmployeeWeekly from "@/components/Analysis/EmployeeWeekly.vue";
import PayDetailView from "@/components/Analysis/PayDetailView.vue";
import MultiSelect from "@/components/common/MultiSelect.vue";

declare var Affinity: any;

export default defineComponent({
    name: "Analysis",
    components: {
        PayDetailView,
        DetailCostReport,
        SimpleAnalysis,
        CostAnalysisWeekly,
        EmployeeWeekly,
        Modal,
        CustomSelect,
        MultiSelect,
        DatePicker,
    },

    setup() {
        const store = useStore();
        //
        const data = ref(<any[]>[]);
        const pageNumber = ref(1);
        const pageSize = ref(20);
        const totalPages = ref(5);
        const pageList = ref(<number[]>[]);
        const recordCount = ref(0);
        const noDataError = ref(false);
        const DatePickerRef = ref<InstanceType<typeof DatePicker>>();
        const SimpleAnalysisRef = ref<InstanceType<typeof SimpleAnalysis>>();
        const CostDetailAnalysisRef =ref<InstanceType<typeof DetailCostReport>>();
        const CostAnalysisWeeklyRef = ref<InstanceType<typeof CostAnalysisWeekly>>();
        const EmployeeWeeklyRef = ref<InstanceType<typeof EmployeeWeekly>>();
        const PayDetailViewRef = ref<InstanceType<typeof PayDetailView>>();

        //
        const selectedUnit = ref('value');
        const unitOptions = ref(['value', 'unit']);

        //
        const fromDate = ref(<Moment | null>null);
        const toDate = ref(<Moment | null>null);
        const employeeList = ref(<number[]>[]);
        const selectableEmployeeList = ref(<any[]>[]);
        const fromDateText = ref("");
        const toDateText = ref("");
        const dateModalTop = ref(0);
        const dateModalLeft = ref(0);
        const datePickerMax = ref(<Moment | null>null);
        const datePickerMin = ref(<Moment | null>null);
        const datePickerTargetDay = ref(<Moment | null>moment());

        // const selectedView = ref('Detailed Cost Report')
        const selectedViewFromDropdown = ref<SelectableView>(SelectableView.weekly);
        const selectedView = ref<SelectableView>(SelectableView.weekly);

        const dataAnalysis = ref(<any[]>[]);
        const options = ref([SelectableView.simple,
                SelectableView.detailed,
                SelectableView.weekly,
                SelectableView.employeeWeekly,
                SelectableView.payDetailView]);

        const app = getCurrentInstance();
        const $user = Affinity.user || app!.appContext.config.globalProperties.$user;

        onMounted(() => {
            if (!fromDate.value || !toDate.value) {
                if (!fromDate.value) {
                    fromDate.value = moment().startOf("month");
                    fromDateText.value = fromDate.value.format("DD/MM/YYYY");
                }
                if (!toDate.value) {
                    toDate.value = moment().endOf("month").startOf("day");
                    toDateText.value = toDate.value.format("DD/MM/YYYY");
                }
            }
            initialRequest();
        });

        async function initialRequest() {
            await store.dispatch(EmployeeActions.getEmployees, $user.EmployeeNo)
                .then((res: any) => {
                    console.log('res', res)

                    selectableEmployeeList.value = res.map((data: any) => {
                        data.isSelected = true
                        return data
                    });
                    employeeList.value = res.map(
                        (employeeDetail: any) => employeeDetail.employeeNo
                    );
                });

            await loadGridDataBySelection();
        }

        async function loadGridDataBySelection() {
            console.log('selectable employee', selectableEmployeeList.value)
            try {
                if (fromDate.value && toDate.value) {
                    if (toDate.value.diff(fromDate.value, "weeks") >= 6) {
                        Dialog.alert(
                            "Selected Date range must not be greater than 6 weeks."
                        );
                        throw "weeks error";
                    }
                }

                // SelectableView.simple,
                //     SelectableView.detailed,
                //     SelectableView.weekly,
                //     SelectableView.employeeWeekly,
                //     SelectableView.payDetailView]
                selectedView.value = selectedViewFromDropdown.value;

                if (selectedView.value === SelectableView.simple) {
                    SimpleAnalysisRef.value!.loadGridData();
                }
                else if (selectedView.value === SelectableView.detailed) {
                    CostDetailAnalysisRef.value!.loadGridData();
                }
                else if (selectedView.value === SelectableView.weekly) {
                    console.log('weekly')
                    CostAnalysisWeeklyRef.value!.loadGridData();
                }
                else if (selectedView.value === SelectableView.employeeWeekly) {
                    EmployeeWeeklyRef.value!.loadDataBySelection();
                }

                else if (selectedView.value === SelectableView.payDetailView) {
                    PayDetailViewRef.value!.loadDataBySelection();
                }
                

            } catch (e) {
                //
                // console.log('e', e)
            }
        }

        async function updateToDateTime($event: any, date: any) {
            let element = $event.target.getClientRects()[0];
            dateModalTop.value = element.bottom;
            dateModalLeft.value = element.left;
            if (fromDate) {
                datePickerMin.value = fromDate
                    .value!.clone()
                    .subtract(1, "day");
                datePickerMax.value = null;
            }
            datePickerTargetDay.value = toDate.value ? toDate.value : moment();

            const d = await openDatePicker();
            toDate.value = d || undefined;
            toDateText.value = toDate.value!.format("DD/MM/YYYY");
        }

        async function updateFromDateTime($event: any, date: any) {
            let element = $event.target.getClientRects()[0];
            dateModalTop.value = element.bottom;
            dateModalLeft.value = element.left;

            if (toDate) {
                datePickerMin.value = null;
                datePickerMax.value = toDate.value!.clone().add(1, "day");
            }

            datePickerTargetDay.value = fromDate.value
                ? fromDate.value
                : moment();

            const d = await openDatePicker();
            fromDate.value = d || undefined;
            fromDateText.value = fromDate.value!.format("DD/MM/YYYY");
        }

        async function openDatePicker() {
            return (await DatePickerRef.value!.toggleShow(
                datePickerTargetDay.value!
            )) as Promise<any>;
        }

        async function exportCSVByView() {
            if (selectedView.value === SelectableView.simple) {
                await SimpleAnalysisRef.value!.exportCSV();
            } else if (selectedView.value === SelectableView.detailed) {
                await CostDetailAnalysisRef.value!.exportCSV();
            } else if (selectedView.value === SelectableView.weekly) {
                await CostAnalysisWeeklyRef.value!.exportCSV();
            } else if (selectedView.value === SelectableView.employeeWeekly) {
                await EmployeeWeeklyRef.value!.exportCSV();
            } else if (selectedView.value === SelectableView.payDetailView) {
                await PayDetailViewRef.value!.exportCSV();
            }
        }

        async function selectGridData(option: SelectableView) {
            selectedViewFromDropdown.value = option;
        }

        function selectUnitOption(newUnit: string) {
            console.log('selected ', newUnit)
            selectedUnit.value = newUnit;
        }

        function selectedEmployeeList() {
            return selectableEmployeeList.value.filter(
                (employee: any) => employee.isSelected
            ).map((employee: any) => employee.employeeNo);
        }


        return {
            DatePickerRef,
            CostDetailAnalysisRef,
            SimpleAnalysisRef,
            CostAnalysisWeeklyRef,
            EmployeeWeeklyRef,
            PayDetailViewRef,
            data,
            dataAnalysis,
            options,
            selectedView,
            selectedViewFromDropdown,
            pageNumber,
            pageSize,
            totalPages,
            pageList,
            noDataError,
            recordCount,
            selectedUnit,
            unitOptions,
            dateModalTop,
            dateModalLeft,
            datePickerMax,
            datePickerMin,
            datePickerTargetDay,
            fromDate,
            toDate,
            fromDateText,
            toDateText,
            employeeList,
            selectableEmployeeList,
            updateToDateTime,
            updateFromDateTime,
            exportCSVByView,
            selectGridData,
            loadGridDataBySelection,
            selectUnitOption,
            selectedEmployeeList,
            SelectableView,
        };
    },
});
