
import 'chartjs-adapter-moment';

import Modal from "@/components/common/Modal.vue";

import {defineComponent, Suspense, ref, watch, onMounted, nextTick, onUnmounted} from "vue";
import moment, {Moment} from "moment";
import Remote from "@/remote/scheduleRequest";
import {EmployeeGetters} from "@/store/employeeModule";
import Dialog from "@/components/dialog";
import {entries} from "lodash";
import currency from 'currency.js';
import Chart from "chart.js/auto/auto.mjs";
import {
    employeePayDetailData, employeePayDetailDataValue,
    getDateMockEmployeeUnit,
    getDateMockEmployeeValue
} from "@/components/Analysis/AnalysisMockData";
import {nameOfDays} from "@/components/common/script";
import CustomSelect from "@/components/common/CustomSelect.vue";
import {AnalysisActions} from "@/store/analysisModule";
import MultiSelect from "@/components/common/MultiSelect.vue";
import { useStore } from 'vuex';
import {PayDetailViewCSVHeader} from "@/components/Analysis/preset";

let MyChart = <any>null;

declare var Affinity: any

interface Option {
    key: string;
    value: string;
    isSelected: boolean;
}


enum DataKeys {
    Z1Scheduled01NormalHours = "z1Scheduled 01 - Normal Hours",
    Z1Scheduled02TimeAndHalf = "z1Scheduled 02 - Time & Half",
    Z1Scheduled03Double = "z1Scheduled 03 - Double",
    Z2Actual01NormalHours = "z2Actual 01 - Normal Hours",
    Z2Actual02TimeAndHalf = "z2Actual 02 - Time & Half",
    Z2Actual03Double = "z2Actual 03 - Double",
    Z3Variance01NormalHours = "z3Variance 01 - Normal Hours",
    Z3Variance02TimeAndHalf = "z3Variance 02 - Time & Half",
    Z3Variance03 = "z3Variance 03",
}

export default defineComponent ({
    name: "PayDetailView",
    components: {
        Dialog,
        CustomSelect,
        Modal,
        MultiSelect,
    },
    props: {
        dayFocus: {
            type: Object
        },
        fromDate: {
            type: Object,
        },
        toDate: {
            type: Object,
        },
        selectedUnit: {
            type: String,
        },
        employeeList: {
            type: Array
        },
        modalIndex: {
            type: Number
        }
    },

    setup(props) {
        console.log('props', props)
        console.log('employeeList', props.employeeList)
        const store = useStore();
        const selectedDate = ref<Moment | null>(null);
        const data = ref<any[]>([]);
        const graphData = ref<any[]>([]);
        const dataHours = ref<any[]>([]);
        const dataUnits = ref<any[]>([]);
        const unitOptions = ref<string[]>(['hours', 'unit']);
        const selectableOption = ref<Option[]>([
            {
                key: 'normalHours',
                value: 'Normal Hours',
                isSelected: true
            }, 
            {
                key: 'timeAndHalf',
                value: 'Time & Half',
                isSelected: true
            }, 
            {
                key: 'double',
                value: 'Double',
                isSelected: true
            }
        ]);
        const calculatedData = ref<any[]>([]);
        
        const displayN = ref<number>(6);
        const displayList = ref<any>({});
        const heading = ref<string[]>(['Scheduled', 'Actual', 'Variance']);
        const secondHeading = ref<string[]>(['Ordinary',	'Time & Half', 'Double', 'Ordinary', 'Time & Half', 'Double', 'Ordinary', 'Time & Half', 'Double']);
        const weekStart = ref<string>('');
        const weekEnd = ref<string>('');
        const fromDate = ref();
        const toDate = ref();
        const noDataError = ref(false);

        onMounted(() => {
        // load initial data
            console.log(' pay detailed view mounted')
            
            nextTick(() => {
                setGraphData();
            })
        });

        onUnmounted(() => {
            MyChart.destroy();
            MyChart = null;
        });

        watch(
            selectableOption, async (selectedUnit) => {
                console.log('selected', selectedUnit);
                await selectChartOption();
            }, { deep: true }
        );

        loadDataBySelection();

        async function loadDataBySelection() {
            console.log('loadDataBySelection in paydetail view')
            Dialog.loading();
            let weekStart = props.fromDate!.clone().format("YYYY-MM-DD");
            let weekEnd = props.toDate!.clone().format("YYYY-MM-DD");


            const res = await store.dispatch(AnalysisActions.fetchPayDetailViewData, {
                weekStart,
                weekEnd,
                employeeNumbers: props.employeeList!.filter((employeeNumber: any) => employeeNumber !== null && employeeNumber !== undefined),
                selectedUnit: props.selectedUnit,
                pageNumber: 1,
                pageSize: 99,
                payCodes: ["01", "02", "03", "04", "05"],
            });

            if (!res) {
                return;
            }

            graphData.value = res.response.map((data: any) => [
                data.CommonName,
                data[DataKeys.Z1Scheduled01NormalHours],
                data[DataKeys.Z1Scheduled02TimeAndHalf],
                data[DataKeys.Z1Scheduled03Double],
                data[DataKeys.Z2Actual01NormalHours],
                data[DataKeys.Z2Actual02TimeAndHalf],
                data[DataKeys.Z2Actual03Double],
                data[DataKeys.Z3Variance01NormalHours],
                data[DataKeys.Z3Variance02TimeAndHalf],
                data[DataKeys.Z3Variance03]
            ]);

            data.value = res.response.map((data: any) => [
                data.CommonName,
                currency(data[DataKeys.Z1Scheduled01NormalHours], {precision: 2}).format(),
                currency(data[DataKeys.Z1Scheduled02TimeAndHalf], {precision: 2}).format(),
                currency(data[DataKeys.Z1Scheduled03Double], {precision: 2}).format(),
                currency(data[DataKeys.Z2Actual01NormalHours], {precision: 2}).format(),
                currency(data[DataKeys.Z2Actual02TimeAndHalf], {precision: 2}).format(),
                currency(data[DataKeys.Z2Actual03Double], {precision: 2}).format(),
                currency(data[DataKeys.Z3Variance01NormalHours], {precision: 2}).format(),
                currency(data[DataKeys.Z3Variance02TimeAndHalf], {precision: 2}).format(),
                currency(data[DataKeys.Z3Variance03], {precision: 2}).format()
            ]);

            nextTick(() => {
                setGraphData();
                Dialog.clear();
            })
        }

        function selectChartOption() {
            console.log('select chart options');

            updateGraphData();
        }

        
        // other helper methods...
        function getGraphData() {
            let labelAndColor = {
                schedule: {
                    ordinary: {
                        label: 'Scheduled - Ordinary',
                        backgroundColor: '#ff6384',
                        data: <any[]>[]
                    },
                    half: {
                        label: 'Scheduled - Time & Half',
                        backgroundColor: '#464531',
                        data: <any[]>[]
                    },
                    double: {
                        label: 'Scheduled - Double',
                        backgroundColor: '#1B5583',
                        data: <any[]>[]
                    }
                },
                actual: {
                    ordinary: {
                        label: 'Actual - Ordinary',
                        backgroundColor: '#93e093',
                        data: <any[]>[]
                    },
                    half: {
                        label: 'Actual - Time & Half',
                        backgroundColor: '#1f77b4',
                        data: <any[]>[]
                    },
                    double: {
                        label: 'Actual - Double',
                        backgroundColor: '#ff7f0e',
                        data: <any[]>[]
                    }
                },
                variance: {
                    ordinary: {
                        label: 'Variance - Ordinary',
                        backgroundColor: '#2ca02c',
                        data: <any[]>[]
                    },
                    half: {
                        label: 'Variance - Time & Half',
                        backgroundColor: '#d62728',
                        data: <any[]>[]
                    },
                    double: {
                        label: 'Variance - Double',
                        backgroundColor: '#9467bd',
                        data: <any[]>[]
                    }
                }
            }

            
            console.log('this data', graphData.value)
            for (const dayData of graphData.value) {
                labelAndColor.schedule.ordinary.data.push(dayData[1])
                labelAndColor.actual.half.data.push(dayData[2])
                labelAndColor.variance.double.data.push(dayData[3])

                labelAndColor.actual.ordinary.data.push(dayData[4])
                labelAndColor.schedule.half.data.push(dayData[5])
                labelAndColor.schedule.double.data.push(dayData[6])

                labelAndColor.variance.ordinary.data.push(dayData[7])
                labelAndColor.variance.half.data.push(dayData[8])
                labelAndColor.variance.double.data.push(dayData[9])
            }

            let selectedGraphData: any[] = []
        
            // update graph data
            // normal hours
            if (selectableOption.value[0].isSelected) {
                selectedGraphData.push(labelAndColor.schedule.ordinary)
                selectedGraphData.push(labelAndColor.actual.ordinary)
                selectedGraphData.push(labelAndColor.variance.ordinary)
            } 

            // time and half
            if (selectableOption.value[1].isSelected) {
                selectedGraphData.push(labelAndColor.schedule.half)
                selectedGraphData.push(labelAndColor.actual.half)
                selectedGraphData.push(labelAndColor.variance.half)
            }

            // double
            if (selectableOption.value[2].isSelected) {
                selectedGraphData.push(labelAndColor.schedule.double)
                selectedGraphData.push(labelAndColor.actual.double)
                selectedGraphData.push(labelAndColor.variance.double)
            }

            return selectedGraphData
        }
        
        function setGraphData() {
            let chartLabels = []
            
            chartLabels = data.value.map((data: any) => {
                return data[0] // employee name
            })
            let payDetailChart = (document as any).getElementById('payDetailChart')
            if (!payDetailChart) {
                return
            }
            let ctx = payDetailChart.getContext('2d');

            let config = {
                type: 'bar',
                data: {
                    labels: chartLabels,
                    datasets: getGraphData(),
                },

                options: {
                    responsive: true,
                }
            }
            if (MyChart) {
                console.log('MyChart', MyChart)
                updateGraphData();
            } else {
                MyChart = new Chart(ctx!, config as any ) as any;
            }

        }

        async function exportCSV() {
            if (noDataError.value) {
                Dialog.alert("No data to export");
                return;
            }

            Dialog.loading();
            // todo may be handle get all request
            const allData: any = data.value;
            Dialog.clear();
            let csvContent = "data:text/csv;charset=utf-8,";

            csvContent += PayDetailViewCSVHeader[0].join(",") + "\r\n" + PayDetailViewCSVHeader[1].join(",") + "\r\n";

            allData.map((rowArray: any) => {
                csvContent += rowArray.map((value: string) => `"${value}"`).join(",") + "\r\n";
            });

            var encodedUri = encodeURI(csvContent);
            var link = document.createElement("a");
            link.setAttribute("href", encodedUri);
            link.setAttribute("download", "Analytic.csv");
            document.body.appendChild(link); // Required for FF

            link.click();
        }

        function updateGraphData() {
            MyChart.data.datasets = getGraphData();
            MyChart.update();
        }

        return {
            selectableOption,
            fromDate,
            toDate,
            
            graphData,
            data,
          
            selectedDate,
            dataHours,
            dataUnits,
            unitOptions,
            calculatedData,
           
            displayN,
            displayList,
            heading,
            secondHeading,
            weekStart,
            weekEnd,
            loadDataBySelection,
            selectChartOption,
            setGraphData,
            exportCSV,
        };
    },
})
